.about-mission {
  padding-top: 80px;
  padding-bottom: 160px;
  background-color: var(--athens);

  &__list {
    --mission-gap: 94px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: calc(var(--mission-gap) * 2);

    @media (max-width: 1024px) {
      --mission-gap: 30px;
    }

    @media (max-width: 768px) {
      --mission-gap: 10px;
    }

    @media (max-width: 576px) {
      display: block;
    }
  }

  &__item {
    text-align: center;
    background-position: top center;
    background-repeat: no-repeat;
    background-size: 48px 48px;
    padding-top: 74px;
    position: relative;

    &:not(:last-child)::after {
      content: "";
      position: absolute;
      right: calc(-1 * var(--mission-gap));
      top: 0;
      width: 1px;
      height: 100%;
      background-image: url("../img/mission-line.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }

    @media (max-width: 576px) {
      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }
  }

  &__item-title {
    margin: 0;
    font-weight: bold;
    font-size: 20px;
    line-height: 1.5;
    color: var(--black);
    margin-bottom: 8px;
  }

  &__item-text {
    margin: 0;
    font-size: 16px;
    line-height: 1.6;
    color: var(--storm);
  }

  &__item--quality {
    background-image: url("../img/like.svg");
  }

  &__item--safety {
    background-image: url("../img/hand.svg");
  }

  &__item--comfort {
    background-image: url("../img/slippers.svg");
  }

  &-form {
    position: relative;
    top: 92px;
    padding: 48px 60px;
    background: var(--white);
    box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05),
      0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
    border-radius: 4px;

    &__content {
      display: flex;
      align-items: flex-end;
      justify-content: center;
    }

    &__field {
      margin-right: 24px;
    }

    &__btn {
      font-size: 14px;
      padding: 13px 31px;
    }
  }

  &__btn {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateY(50%) translateX(-50%);

    & a {
      padding: 16px 111px;
    }
  }

  &--dark {
    position: relative;
    padding: 80px 0 100px;
    background-color: var(--black);

    & p {
      color: rgba(255, 255, 255, 0.6);
    }

    & .about-about-about-mission__item-title {
      color: var(--white);
    }
  }
}
